* , *:before, *:after{
  box-sizing:border-box;
  -moz-box-sizing:border-box;
  -webkit-box-sizing:border-box;
  -ms-box-sizing:border-box;
} 
html, body {
margin: 0;
padding: 0;
font-family: 'Risque', cursive;
background: #222020;
}
.header {
display: flex;
flex-direction: column;
min-height: 10vh;
text-align: center;
}
.header h1 {
font-size: 2rem;
color: #f05151;
}
.main {
display: flex;
flex-direction: column;
}
.container {
display: flex;
flex-direction: column;
align-items: center;
justify-content: center;
}
.background {
display: flex;
flex-direction: column;
background-position: center;
background-repeat: no-repeat;
background-size: cover;
height: 80vh;
min-height: 500px;
}
#intro {
display: flex;
flex-direction: column;
text-align: center;
align-items: center;
justify-content: center;
}
#horror-wrap {
display: flex;
flex-direction: column;
text-align: center;
width: 90vw;
}
.hidden {
opacity: 0;
transition: opacity 0.5s ease-in-out;
}
.fade-in {
opacity: 0.8;
-webkit-transition: opacity 1s ease-in-out;
}
.cast {
display: flex;
flex-direction: column;
position: absolute;
width: 90vw;
}
.title {
display: flex;
flex-direction: column;
position: absolute;
width: 90vw;
top: 45vh;
color: red;
font-size: 2rem;
}
.tagline {
display: flex;
flex-direction: column;
position: absolute;
width: 90vw;
top: 80vh;
color: #ef5151;
font-size: 1rem;
font-style: italic;
}
.tagline p {
text-shadow: 1px 2px 3px #000;
}
h1, h2, h3, h4 {
font-family: 'Griffy', cursive;
color: #ff81a8;
text-shadow: 1px 2px 2px #000;
}
h2 {
font-size: 1.75rem;
}
h3 {
font-size: 1.6rem;
}
h4 {
font-size: 1.5rem;
}
#intro h2,
#intro h3,
#intro h4,
.social h3 {
color: #5ec16e;
}
#intro h2, #intro h4 {
margin-block-start: 0.5em;
margin-block-end: 0.8em;
}
#intro h4 {
font-family: 'Risque', cursive;
}
/* Generator controls */
#controls {
display: flex;
flex-direction: column;
text-align: center;
align-items: center;
margin: 30px 0;
}
.generator {
display: flex;
flex-direction: column;
}
.generator-first {
display: flex;
flex-direction: column;
align-items: center;
}
#capture-btn, #generate-btn {
display: none;
width: 50vw;
margin-bottom: 3vh;
}
#start-btn {
width:70vw;
}
#start-btn, #capture-btn, #generate-btn {
font-family: 'Risque', cursive;
background: #b3329d;
color: #e4d0d0;
border: none;
border-radius: 5px;
font-size: 1rem;
cursor: pointer;
}
#start-btn:hover,
#capture-btn:hover,
#generate-btn:hover
{
background: #4c32b3;
}
#social-links {
display: none;
flex-direction: column;
align-items: center;
justify-content: center;
text-align: center;
}
.share-links ul {
display: flex;
flex-direction: row;
list-style-type: none;
margin-block-start: 0;
padding-inline-start: 0;
}
.share-links li {
background: #b3329d;
width: 22vw;
height: 50px;
line-height: 50px;
border-radius: 5px;
}
.share-links li:hover {
background: #4c32b3;
}
.share-links a:nth-child(n+2) {
margin-left: 3vw;
}
.share-links a {
color: #e4d0d0;
text-decoration: none;
font-size: 1.8rem;
}
.share-links span {
display: none;
}
/* Media queries */
@media only screen and (min-width: 200px) {
.title {
  top: 40vh;
}
}
@media only screen and (min-width: 300px) {
.tagline {
  font-size: 1.5rem;
}
}
@media only screen and (min-width: 400px) {
.generator {
  flex-direction: row;
}
#capture-btn, #generate-btn {
  width: 30vw;
  margin-bottom: 0;
  margin-left: 3vw;
}
}
@media only screen and (min-width: 500px) {
#intro {
  top: 25vh;
}
h1 {
  font-size: 4rem;
}
h2 {
  font-size: 2rem;
}
h3 {
  font-size: 3rem;
}
h4 {
  font-size: 2rem;
}
#start-btn {
  width: 50vw;
}
.share-links span {
  display: inline-flex;
}
.share-links a {
  font-size: 1rem;
}
}
@media only screen and (min-width: 600px) {
h2 {
  font-size: 3rem;
}
}
@media only screen and (min-width: 768px) {
.title {
  top: 38vh;
}
h2 {
  font-size: 4rem;
} 
}
@media only screen and (max-height: 500px) {
.title {
  top: 269px;
}
.tagline {
  top: 500px;
}
}
